import { useState, useEffectt } from "react";
import Home from "./Home";
import Projects from "./Projects";
import About from "./About";
import Resume from "./Resume";
import styles from "../css/Content.module.scss";
import "../css/transitions.scss";
import { CSSTransition } from "react-transition-group";

const Content = ({ page, className }) => {
  return (
    <div className={className}>
      <div className={styles.header}>Nathan Yik</div>
      <CSSTransition
        in={page.home}
        appear={true}
        timeout={750}
        classNames="fade"
        unmountOnExit
      >
        <Home />
      </CSSTransition>
      <CSSTransition
        in={page.projects}
        appear={true}
        timeout={750}
        classNames="fade"
        unmountOnExit
      >
        <Projects />
      </CSSTransition>
      <CSSTransition
        in={page.about}
        appear={true}
        timeout={750}
        classNames="fade"
        unmountOnExit
      >
        <About />
      </CSSTransition>
      <CSSTransition
        in={page.resume}
        appear={true}
        timeout={750}
        classNames="fade"
        unmountOnExit
      >
        <Resume />
      </CSSTransition>
    </div>
  );
};

export default Content;
